import React, { useEffect, useState } from 'react';
import { Routes, Route, useNavigate } from "react-router-dom";
import axios from 'axios';
import '../scss/Sillok.scss'

function Sillok() {
    let [sillok,setSillok] = useState([]);
    useEffect(getAxios,[])

    return (        
    <section className="sillok">
        <div className="inner">
            <h1 className='sectionTitle'>충&nbsp;이&nbsp;실&nbsp;록</h1>
            <div className='card-container'>
                {
                    sillok.map(obj => <Card obj={obj}/>)
                }
            </div>
        </div>
    </section>
    )

    function getAxios() {
        axios
        .get('https://atmosg.github.io/chung2sillok/sillok.json')
        .then(response => setSillok([...response.data]))
        .catch(error => console.log(error))
    }
}

function Card(props) {
    let navigate = useNavigate();

    return(
        <div className='card' onClick={(e)=>{navigate(`/sillok/${props.obj.id}`)}}>
            <h2>{props.obj.era}</h2>
            <h3>{props.obj.title}</h3>
        </div>
    )
}

export default Sillok;